<template>
  <div>
    <h2>QR.CSS Buttons</h2>
    <div class="btn-box2">
      <button v-for="n in 10" :key="n" :class="['qr-button', `qr-btn-${n}`]" @click="showCSS(`qr-btn-${n}`)">Hover Me</button>
    </div>
    <div v-if="currentCSS" class="css-display">
      <pre>{{ currentCSS }}</pre>
      <button @click="copyCSS">Copy CSS</button> {{ copyMessage }}
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      currentCSS: ' ',
      copyMessage: '',
      cssStyles: {
        'qr-btn-1': `
.qr-btn-1 {
  background: linear-gradient(45deg, #ff00cc, #3333ff);
}
.qr-btn-1:hover {
  background: linear-gradient(45deg, #cc0099, #1a1a66);
}`,
        'qr-btn-2': `
.qr-btn-2 {
  background: linear-gradient(45deg, #ff7e5f, #feb47b);
}
.qr-btn-2:hover {
  background: linear-gradient(45deg, #cc664a, #b26a58);
}`,
        'qr-btn-3': `
.qr-btn-3 {
  background: linear-gradient(45deg, #34e89e, #0f3443);
}
.qr-btn-3:hover {
  background: linear-gradient(45deg, #2aa878, #0a2328);
}`,
        'qr-btn-4': `
.qr-btn-4 {
  background: linear-gradient(45deg, #ff9a9e, #fad0c4);
}
.qr-btn-4:hover {
  background: linear-gradient(45deg, #cc7a7e, #b28a94);
}`,
        'qr-btn-5': `
.qr-btn-5 {
  background: linear-gradient(45deg, #00c6ff, #0072ff);
}
.qr-btn-5:hover {
  background: linear-gradient(45deg, #0094cc, #004db2);
}`,
        'qr-btn-6': `
.qr-btn-6 {
  background: linear-gradient(45deg, #e1eec3, #f05053);
}
.qr-btn-6:hover {
  background: linear-gradient(45deg, #b3bda0, #b33c3f);
}`,
        'qr-btn-7': `
.qr-btn-7 {
  background: linear-gradient(45deg, #f7971e, #ffd200);
}
.qr-btn-7:hover {
  background: linear-gradient(45deg, #c77618, #b29d00);
}`,
        'qr-btn-8': `
.qr-btn-8 {
  background: linear-gradient(45deg, #8e2de2, #4a00e0);
}
.qr-btn-8:hover {
  background: linear-gradient(45deg, #6a20a6, #340099);
}`,
        'qr-btn-9': `
.qr-btn-9 {
  background: linear-gradient(45deg, #ff416c, #ff4b2b);
}
.qr-btn-9:hover {
  background: linear-gradient(45deg, #cc3356, #b23822);
}`,
        'qr-btn-10': `
.qr-btn-10 {
  background: linear-gradient(45deg, #6a11cb, #2575fc);
}
.qr-btn-10:hover {
  background: linear-gradient(45deg, #4a0d99, #1b53b2);
}`
      }
    }
  },
  methods: {
    showCSS (buttonClass) {
      this.currentCSS = this.cssStyles[buttonClass]
    },
    copyCSS () {
      const el = document.createElement('textarea')
      el.value = this.currentCSS
      document.body.appendChild(el)
      el.select()
      document.execCommand('copy')
      document.body.removeChild(el)
      this.copyMessage = 'CSS copied.'
    }
  }
}
</script>

<style scoped>
/* Common button styles */
.qr-button {
  padding: 15px 30px;
  font-size: 16px;
  color: #fff;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-block;
  margin: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}
.qr-button:hover {
  box-shadow: none;
}

/* Gradient Hover Buttons */
.qr-btn-1 {
  background: linear-gradient(45deg, #ff00cc, #3333ff);
}
.qr-btn-1:hover {
  background: linear-gradient(45deg, #cc0099, #1a1a66);
}

.qr-btn-2 {
  background: linear-gradient(45deg, #ff7e5f, #feb47b);
}
.qr-btn-2:hover {
  background: linear-gradient(45deg, #cc664a, #b26a58);
}

.qr-btn-3 {
  background: linear-gradient(45deg, #34e89e, #0f3443);
}
.qr-btn-3:hover {
  background: linear-gradient(45deg, #2aa878, #0a2328);
}

.qr-btn-4 {
  background: linear-gradient(45deg, #ff9a9e, #fad0c4);
}
.qr-btn-4:hover {
  background: linear-gradient(45deg, #cc7a7e, #b28a94);
}

.qr-btn-5 {
  background: linear-gradient(45deg, #00c6ff, #0072ff);
}
.qr-btn-5:hover {
  background: linear-gradient(45deg, #0094cc, #004db2);
}

.qr-btn-6 {
  background: linear-gradient(45deg, #e1eec3, #f05053);
}
.qr-btn-6:hover {
  background: linear-gradient(45deg, #b3bda0, #b33c3f);
}

.qr-btn-7 {
  background: linear-gradient(45deg, #f7971e, #ffd200);
}
.qr-btn-7:hover {
  background: linear-gradient(45deg, #c77618, #b29d00);
}

.qr-btn-8 {
  background: linear-gradient(45deg, #8e2de2, #4a00e0);
}
.qr-btn-8:hover {
  background: linear-gradient(45deg, #6a20a6, #340099);
}

.qr-btn-9 {
  background: linear-gradient(45deg, #ff416c, #ff4b2b);
}
.qr-btn-9:hover {
  background: linear-gradient(45deg, #cc3356, #b23822);
}

.qr-btn-10 {
  background: linear-gradient(45deg, #6a11cb, #2575fc);
}
.qr-btn-10:hover {
  background: linear-gradient(45deg, #4a0d99, #1b53b2);
}

/* Style for the CSS display section */
.css-display {
  background-color: #f1f1f1;
  border: 1px solid #ccc;
  padding: 20px;
  margin-top: 20px;
  border-radius: 5px;
}

.css-display pre {
  background-color: #fff;
  border: 1px solid #ddd;
  padding: 10px;
  border-radius: 5px;
  overflow-x: auto;
}

.css-display button {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.css-display button:hover {
  background-color: #45a049;
}
</style>
